.banner-img {
  margin-top: 0px;
}

.bannar-row {
  white-space: normal;
  flex-wrap: nowrap;
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 0px !important;
}
.w-100 {
  height: 70vh !important;
  max-height: 70vh !important;
}
.carousel-control-next-icon,
.carousel-control-prev-icon {
  background-color: #000;
}
.carousel-caption {
  bottom: 10.25rem;
}
.banner-container {
  margin: 0 !important;
  padding: 0% !important;
  background-color: #191818;
  width: 100% !important;
  max-width: 100% !important;
  height: 500px;
}

.home-page h1 {
  color: gray;
  font-family: "Playfair Display", serif;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.home-page .card {
  background-color: rgba(128, 128, 128, 0.097);
  flex-basis: 45%;
  margin-bottom: 20px;
}
.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.home-page .card-price {
  color: green;
  font-weight: bold;
}
.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}

.home-page .card img:hover {
  transform: scale(0.9);
}

.home-page .card-img-top {
  height: 300px !important;
}
/* ==================
filters
============== */
.filters h4 {
  margin-top: 100px;
  color: gray !important;
  border-bottom: 1px solid rgba(128, 128, 128, 0.337);
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 8px;
}

.ant-checkbox-wrapper {
  margin: 2px;
}
.ant-radio-wrapper {
  margin-bottom: 5px !important;
  margin-left: 10px;
}

.filters button {
  background-color: black;
  width: 100%;
  border: none;
  border-radius: 0;
  margin-top: 20px;
}

.loadmore {
  color: green;
  font-weight: bold;
  font-size: 20px !important;
}
/* ipadPro media query 1030px */
@media only screen and (max-width: 1030px) {
  .w-100 {
    height: 35vh !important;
    max-height: 35vh !important;
  }
  .carousel-caption {
    bottom: 11.25rem;
  }
}

/* ipad media query 768 */

@media only screen and (max-width: 786px) {
  .home-page .filters {
    flex: 0 0 auto;
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .home-page .filters h4 {
    font-size: 20px;
  }
  .carousel-caption {
    bottom: 7.25rem;
  }
  .home-page .card {
    flex-basis: 100%;
  }
}

/* Media query for Mobile Devices */
@media only screen and (max-width: 500px) {
  .home-page {
    margin: 0% !important;
    width: 100% !important;
  }
  .home-page .home-product {
    margin: 0% !important;
    margin-bottom: 20px !important;
    width: 100% !important;
  }
  .products-main-home {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
  }
  .home-all-product-heading {
    font-size: 57px;
    /* margin: 12px 0px !important; */
  }
  .offcanvas-title {
    width: 100% !important;
  }
  .mobile-nav-filter .offcanvas.offcanvas-end.show {
    width: 65%;
  }
  .mobile-nav-filter-headinh-icon-a {
    width: 65% !important;
    max-width: 65% !important;
  }
  .mobile-nav-filter-headinh-icon-button {
    width: 20% !important;
    max-width: 20% !important;
  }

  .mobile-nav-filter-headinh-icon {
    display: flex !important;
    justify-content: space-between;
  }

  .carousel-caption {
    bottom: 0.25rem;
    left: 0;
    width: 100%;
  }
  .w-100 {
    height: 30vh !important;
    max-height: 30vh !important;
  }
  .home-page .filters {
    width: 100%;
    margin-bottom: 30px;
    padding: 0;
    border-bottom: 2px solid gray;
  }
  .home-page .filters h4 {
    margin-top: 10px;
    margin-bottom: 19px;
    font-size: 42px;
  }
  .ant-checkbox-wrapper {
    font-size: 20px;
  }
  .filters button {
    margin-bottom: 10px;
  }
}
