@media only screen and (max-width: 768px) {
  .product-main {
    margin: 0%;
    margin-top: 50px;
  }
  .product-main .admin-product-list h1 {
    margin-left: 150px;
    margin-top: 50px;
    font-size: 50px;
    font-weight: bold;
  }
  .product-div {
    width: 100% !important;
  }
  .product-main .product-div .product-link {
    width: 100% !important;
  }
  .product-main .product-div .product-link .product-list-admin-card {
    width: 45rem !important;
  }
}

@media only screen and (max-width: 500px) {
  .product-main {
    margin: 0 !important;
    margin-top: 30px !important;
  }
  .admin-product-list {
    padding: 0% !important;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100% !important;
    margin-top: 20px !important;
  }
  .admin-product-list .product-list-admin-card {
    margin: 0 !important;
    margin-bottom: 25px !important;
    padding: 0px 10px !important;
    width: 100% !important;
  }
}
