@media only screen and (max-width: 768px) {
  .admin-updateproduct-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
  }
  .admin-updateproduct-content .btn {
    width: 100% !important;
  }
}

@media only screen and (max-width: 500px) {
  .admin-updateproduct-main {
    margin: 0 !important;
    margin-top: 30px !important;
  }
  .admin-updateproduct-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100% !important;
    margin-top: 20px !important;
  }
  .product-main .admin-product-list h1 {
    margin-left: 50px !important;
    margin-top: 50px !important;
    font-size: 29px !important;
    font-weight: bold !important;
  }
  .product-main .product-div .product-link .product-list-admin-card {
    width: 23rem !important;
  }
}
