@media only screen and (max-width: 786px) {
  .admin-order-main {
    margin: 0 !important;
    margin-top: 50px !important;
    margin-bottom: 20px !important;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap !important;
    box-sizing: border-box;

    overflow-x: auto;
    max-width: 100%;
  }
  .admin-order-content {
    padding: 0% !important;
    box-sizing: border-box !important;

    width: 100% !important;
    margin-top: 20px !important;
    overflow-x: auto !important;
    max-width: 100% !important;
  }
  .admin-order-content .border .shadow {
    overflow-x: auto;
    max-width: 100%;
  }
  .admin-order-content .border .shadow table {
    width: 100%;
  }
  .admin-order-content .admin-order-table {
    width: 100% !important;
    margin-top: 50px;
    margin-bottom: 50px;
  }
}
