.banner-about-img {
  margin: 0%;
  padding: 0%;
  width: 100%;
  max-width: 100%;
  max-height: 75vh !important;
}
.banner-about-img img {
  margin: 0%;
  padding: 0%;
  width: 100%;
  max-width: 100%;
  height: 75vh !important;
  max-height: 75vh;
}

/* Cat Page Style */

.card {
  background-color: #ffffff;
  border: 1px solid #d0d0d0;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
  text-align: center;
}

.card:hover {
  transform: translateY(-5px);
}

.cat-btn {
  text-decoration: none;
  color: #333;
  font-weight: bold;
}
@media only screen and (max-width: 1024px) {
  .banner-about-img {
    max-height: 31vh;
  }
  .banner-about-img img {
    max-height: 31vh;
  }
}
@media only screen and (max-width: 768px) {
  .banner-about-img {
    max-height: 31vh;
  }
  .banner-about-img img {
    max-height: 31vh;
  }
}
