.category {
  flex-wrap: wrap;
  margin-top: 80px !important;
  justify-content: space-between;
}
.category .card {
  background-color: rgba(128, 128, 128, 0.097);
  flex-basis: 45%;
  margin-bottom: 20px;
}
.category .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.category .card-price {
  color: green;
  font-weight: bold;
}
.category button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.category .card-text {
  color: rgb(90, 89, 89);
}

.category .card img:hover {
  transform: scale(0.9);
}

.category .card-img-top {
  height: 300px !important;
}

/* ... (your existing CSS rules) ... */

/* Media query for iPad devices in portrait mode */
@media (max-width: 768px) and (orientation: portrait) {
  /* ... (your existing media query rules) ... */

  .category .card-buttons {
    display: flex;
    justify-content: space-between;
  }

  .category .card-buttons button {
    width: 48%; /* Adjust width as needed */
  }

  /* Adjust other styles as needed */
}

/* Media query for smaller screens */
@media (max-width: 768px) {
  .category .card {
    flex-basis: 100%;
  }
}
