@media only screen and (max-width: 768px) {
  .admin-order-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0% !important;
    box-sizing: border-box !important;
    width: 100% !important;
    margin-top: 50px !important;
    overflow-x: hidden !important;
    max-width: 100% !important;
  }
  .manage-cat-form {
    width: 100% !important;
  }
  .cat-table {
    width: 100% !important;
  }
}
@media only screen and (max-width: 500px) {
  .admin-create-cat-main {
    margin: 0% !important;
  }
  .manage-cat-form {
    width: 100% !important;
    padding: 5px 5px !important;
    margin-bottom: 20px !important;
  }
  .manage-cat-heading {
    margin-top: 30px;
    text-align: center;
  }
  .manage-cat-form button {
    width: 100%;
    justify-content: center;
  }

  .admin-create-cat-main form {
    width: 100% !important;
    display: flex;
    flex-direction: column;
  }
  .admin-create-cat-main .w-75 {
    width: 100% !important;
  }
}
