.product-details {
  margin-top: 100px !important;
}

.product-details-info {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
}

.product-details-info h6 {
  margin-bottom: 20px !important;
}

.similar-products .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
}

.similar-products .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.similar-products .card-price {
  color: green;
  font-weight: bold;
}
.similar-products button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.similar-products .card-text {
  color: rgb(90, 89, 89);
}

.similar-products .card img:hover {
  transform: scale(0.9);
}

.similar-products .card-img-top {
  height: 300px !important;
}

@media only screen and (max-width: 768px) {
  .similar-products {
    margin: 0% !important;
  }
  .similar-products h4 {
    margin: 35px auto;
    font-size: 33px;
  }

  .similar-products .card {
    width: 353px;
  }
}
/* Mobile Devices */
@media only screen and (max-width: 500px) {
  .product-details {
    padding: 0px 0px 0px 0px !important;
    margin: 0px 0px 0px 0px !important;
    margin-top: 30px !important;
  }
  .product-details img {
    margin-bottom: 22px;
  }
  .product-details-info h1 {
    font-size: 30px;
    font-weight: bolder;
    line-height: 0.5;
    margin-top: 20px;
    text-align: left !important;
    justify-content: start !important;
    margin-bottom: 0px;
  }
  .similar-products {
    padding: 0px !important;
    margin: 0px !important;
  }
  .similar-products .card {
    background-color: rgba(128, 128, 128, 0.097);
    width: 20rem;
  }
}
